// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './vuex/index.js'
import api from '@/api/index'
import { axios , http, fileHttp, http_json } from '@/lib/request'
import echarts from 'echarts'



Vue.prototype.$api = api //调用api数据接口地址
Vue.prototype.$echarts = echarts //继承图表
Vue.prototype.$http = http //继承axios
Vue.prototype.$axios = axios //继承axios
Vue.prototype.$fileHttp = fileHttp //继承上传图片
Vue.prototype.$http_json  = http_json
import '@/assets/css/common.css'//公共样式
import 'vant/lib/vant-css/index.css';//vant样式
// import 'vant/lib/vant-css/button.css';

import { Button,Tabbar, TabbarItem,Cell, CellGroup,Swipe, SwipeItem ,Popup ,Toast,Picker, Tab, Tabs ,Collapse ,CollapseItem, Uploader, Dialog, List, DatetimePicker    } from 'vant';

Vue.use(Button).use(Tabbar).use(TabbarItem).use(Popup).use(Toast).use(Swipe).use(SwipeItem).use(Picker).use(Cell).use(CellGroup).use(Tab).use(Tabs).use(Collapse).use(CollapseItem).use(Uploader).use(Dialog).use(List).use(DatetimePicker);



Vue.config.productionTip = false;


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



