import axios from 'axios'
import Qs from 'qs'

/* axios配置 */

const instance = axios.create({

    //baseURL: 'http://127.0.0.1:8080/',  //配置接口地址
    timeout: 20000,  //响应时间
    headers: {
      //  'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',  //配置请求头
        'Accept': 'application/json'
    }
});

// http request 拦截器
instance.interceptors.request.use(config => {
    var userInfos =  localStorage.getItem('zhxy_user');
    config.headers['token'] = userInfos ?  JSON.parse(userInfos).token : '';
    config.headers['registrationId'] = localStorage.getItem('registrationId');
    return config
},err => {
    console.log("err",err);
    return Promise.reject(err)
})

//返回一个Promise(默认发送post请求)
/**
 * option.method  类型  post
 * option.url   地址
 * params 传参
 * **/

const http = (option, params) => {
    var userInfos =  localStorage.getItem('zhxy_user');
    if(typeof option == 'undefined'){
       option = {};
    }
    option.method = option.method || 'post';
    params.token = userInfos ?  JSON.parse(userInfos).token : '';
    params.registrationId = localStorage.getItem('registrationId');
    console.log(params)
    // console.log(params)
    return new Promise((resolve, reject) => {
        instance({method:option.method,url:option.url,data:Qs.stringify(params)}).then(response => {
            resolve(response.data);
           }, err => {
               reject(err);
           })
           .catch((error) => {
              reject(error)
        })
    })
}
const http_json = (url, params) => {
    var userInfos =  localStorage.getItem('zhxy_user');
    let config = {
        headers: {'Content-Type': 'application/json'}
    }
    params.token = userInfos ?  JSON.parse(userInfos).token : '';
    params.registrationId =  localStorage.getItem('registrationId');
     return new Promise((resolve, reject) => {
        axios.post(url, params, config).then(response => {
            resolve(response.data);
        },err => {
            reject(err);
        }).catch((error) => {
            reject(error)
        })
     })
}
const fileHttp = (url, params) =>{
    var userInfos =  localStorage.getItem('zhxy_user');
    params.token = userInfos ?  JSON.parse(userInfos).token : '';
    params.registrationId = localStorage.getItem('registrationId');
    let config = {
        headers: {'Content-Type': 'multipart/form-data'}
    }
     return new Promise((resolve, reject) => {
        axios.post(url, params, config).then(response => {
            resolve(response.data);
        },err => {
            reject(err);
        }).catch((error) => {
            reject(error)
        })
     })
 }
export {
    axios,
    http,
    fileHttp,
    http_json
}


